@import 'normalize';
@import 'colors';
@import 'typography';
@import 'variables';
@import 'mixins';

body {
  @include no-edges;
  background-color: $bg-light;
  font-family: $body-font;
}
h1, h2, h3, h4, h5 {
  font-family: $header-font;
  margin: 0;
}
h1 {
  font-size: 3em;
}
h3 {
  margin: .5em 0;
}
main {
  display: flex;
  flex-flow: column nowrap;
  background-color: $bg-bold;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;

  header.intro {
    @include flex-center-all;
    height: 10em;
    text-align: center;
    color: $text-light;
  }
  header.main-head {
    box-sizing: border-box;
    height: 4em;
    width: 100%;
    background-color: $bg-bold;
    color: $text-light;
    padding: 1em;
  }
}
.back-btn {
  width: 100%;
  button {
    width: 100%;
  }
}
.main-body {
  padding: 0 .5em;
}
.pokebreeder {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  background-color: $bg-light;
  padding-bottom: 7em;
  .count-box {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2em;
    transition: all ease-in .5s;
    background-color: $bg-bold;
    display:flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    margin-top: auto;
    box-sizing: border-box;
    padding: .5em .5em 6em .5em;
    color: $text-light;
    .saved-list {
      ul{
        @include no-edges;
        width: 100%;
        height: 24em;
        margin: .5em 0 5em 0;
        li {
          box-sizing: border-box;
          padding: .25em;
          width: 100%;
          text-align: left;
          display: flex;
          flex-flow: row nowrap;
          justify-content: space-between;
          height: 2em;
          background-color: lighten($bg-bold, 10%);
        }
        li:nth-of-type(2n){
          background-color: $bg-bold;
        }
      }
    }
  }
}
.count-box.shift-height {
  transition: all ease-in .5s;
  height: 32em;
}
.count-box::before{
  display:block;
  position: absolute;
  top: -.5em;
  left: calc(50% - 1.5em);
  background-color: $bg-bold;
  width: 3em;
  height: .5em;
  border-radius: 10px 10px 0 0;
  content: '=';
  text-align:center;
  color: $text-light;
}
.remove{
  font-weight: bold;
  font-family: $header-font;
  margin: 0;
  padding: .25em .5em;
  background-color: $bg-accent;
  border-radius: 50%;
}
.error-msg {
  font-size: .9em;
  color: $bg-accent;
  font-weight: 700;
  margin-bottom: .5em;
}
button {
  height: 3rem;
  padding: 0 1rem;
  background-color: $bg-accent;
  color: $text-light;
  font-family: $header-font;
  font-weight: 700;
  border: none;
  cursor: pointer;
  &.btn-small{
    background-color: $bg-light;
    color: $text-dark;
    height: 1.5em;
  }
}
.sort-links {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: .5em 0;
  & select {
    margin-left: .5em;
    background-color: $bg-light;
  }
}
.page-buttons {
  @include no-edges;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  button {
    padding: .5em .5em;
    background-color: $bg-bold;
    border-radius: 50%;
    width: 2em;
    height: 2em;
    &.selected {
      background-color: $bg-accent;
    }
  }
}
footer {
  position: fixed;
  box-sizing: border-box;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: .5em;
  background-color: $bg-bold;
  a{
    @include flex-center-all;
    margin-right: .5rem;
    font-family: $header-font;
    font-weight: 900;
    font-size: 3rem;
    height: 3.5rem;
    width: 3.5rem;
    border-radius: 50%;
    background-color: $bg-accent;
    color: $text-light;
    text-decoration: none;
  }
}

.hidden {
  display: none;
}

.instructions {
  text-align: center;
  color: $text-light;
  ol {
    font-size: 1.3rem;
    margin: 2rem auto;
    width: 60%;
    text-align: left;
    li {
      margin-bottom: 1rem;
    }
  }
}

.info {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  padding: calc(5% + 2em);
  z-index: 100;
  background: 
      $modal-bg no-repeat center,
      $shaded no-repeat center;
  background-size: 90% 90%, 100%;
}

ul {
  @include no-edges;
  list-style-type: none;
  li {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
  }
}
ul.type-list{
  list-style-type: none;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  text-align: center;
  li {
    display: inline-block;
    width: 32%;
    border-radius: 10px;
    color: $text-light;
    padding: .5em 0;
    margin-bottom: .25em;
  }
}
button[data-gender='d'] {
  background-color: $bg-ng;
  pointer-events: none;
  background-color: $bg-light;
  border: 1px solid $bg-dark;
  color: $text-dark;
  @include gender-button;
}
button[data-gender='genderless'] {
  background-color: $bg-ng;
  @include gender-button;
}
button[data-gender='male'] {
  background-color: $bg-male;
  @include gender-button;
}
button[data-gender='female'] {
  background-color: $bg-female;
  @include gender-button;
}
.button-set {
  display: flex;
  margin: 0 0 0 auto;
  flex-flow: row nowrap;
  justify-content: flex-end;
}
.fancy-parent-container {
  display:flex;
  flex-flow: row wrap;
  justify-content: space-around;
  .fancy-parent {
    position: relative;
    text-align: center;
    img {
    background-color: darken($bg-light, 10%);
    border-radius: 50%;
    background-size: 80% 80%;
    width: 96px;
    height: 96px;
    @include gender-bg;
    }
    .parent-name {
      position: absolute;
      left: 0;
      bottom: -.8em;
      width: 100%;
      font-size: .8em;
      font-weight: 700;
    }
    .gender-symbol {
      color: $text-dark;
      position: absolute;
      right: 0;
      bottom: 5px;
      font-size: 1.2em;
      text-shadow: 0px 1px 0px $text-dark, 1px 0px 0px $text-dark;
    }
  }
}
.parent-box {
  position: relative;
  min-height: 160px;
  margin: .5em 0;
  padding: .5em;
  background-color: darken($bg-light, 10%);
  border: 1px inset;
  border-radius: 5px;
  text-align: right;
  img {
    float: left;
    background-color: rgba($bg-dark, .1);
    border-radius: 50%;
    background-size: 80% 80%;
    width: 144px;
    height: 144px;
  }
  & h4 {
    position: absolute;
    bottom: 0;
    left: 0;
    margin: .5em;
  }
  & h3 {
    position: absolute;
    top: 0;
    right: 5px;
    margin-top: 0;
    font-size: 2em;
  }
  & li{
    justify-content: flex-end;
  }
  .parent-types {
    margin-top: 2.5em;
  }
  .type-label-sm {
    display: inline;
    padding: .25em;
    margin: 3em .25em;
    border-radius: 5px;
    color: $text-light;
    text-shadow: 0px 1px 0px $text-dark, 1px 0px 0px $text-dark;
  }
  .parent-gender{
    margin: 0;
    padding: .5em;
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: 0 0 5px 0;
    font-weight: 900;
    text-transform: uppercase;
    @include gender-bg;
  }
}
.big-text {
  font-size: 4em;
  font-weight: bold;
  font-family: $header-font;
  @include no-edges;
  @include flex-center-all;
  &:nth-child(4){
    width: 100%;
    margin-top: 1em;
  }
}